@import 'vars';
@import 'mixins';

.page-title {
    margin: 30px 0 25px 0;
}

.backg-container {
    padding-bottom: 50px;
}

.catalog {
    display: flex;
    &__left {
        background: #fff;
        border-right: 1px solid #ececec;
    }
    &__right {
        background: #fff;
        width: 100%;
        // padding: 0 15px 10px 10px;
    }
    &__right-head {
        padding-left: 35px;
        padding-bottom: 10px;
        padding-right: 15px;
    }
    &__right-head-row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.filters-sidebar {
    width: 240px;
    background: #fff;
    padding: 0 7px;
    padding-bottom: 70px;
}

.preferences {
    padding: 15px 0 18px 10px;
    color: #a6a6a6;
    font-size: 16px;
    font-weight: 400;
    &__text {
        display: inline-block;
        margin-right: 15px;
    }
}

.filters-reset {
    display: inline-block;
    &-input {
        border: none;
        background: transparent;
        outline: none;
        cursor: pointer;
        padding: 0;
        color: #ef9a9a;
        font-size: 14px;
        font-weight: 400;
        background-image: url('../img/icon/reset.svg');
        background-repeat: no-repeat;
        background-position: 0 center;
        padding-left: 20px;
        transition: .3s;
        &:hover {
            color: #ef5350;
            background-image: url('../img/icon/reset-hover.svg');
        }
        &:active {
            color: #eb7876;
            background-image: url('../img/icon/reset-active.svg');
        }
    }
}

.filter {
    &__title {
        color: #2b2b2b;
        font-size: 18px;
        font-weight: 700;
        font-family: $frank-font;
        background: #f7f7f7;
        padding: 10px 15px 10px 15px;
        margin-bottom: 15px;
    }
    &__checkbox-group {
        padding-left: 15px;
        padding-bottom: 20px;
    }
    &__checkbox-row {
        margin-bottom: 5px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__checkbox+label {
        color: map_get($gray, default);
        font-size: 14px;
        font-weight: 400;
        line-height: 16px;
        cursor: pointer;
        max-width: 185px;
        display: inline-block;
        &:hover {
            &::before {
                border: 1px solid #767676;
            }
        }
    }
}

.filter-info-text {
    color: map_get($gray-light, default);
    font-size: 11px;
    font-weight: 400;
    line-height: 14px;
    padding-left: 25px;
}



.product-categories {
    padding-left: 30px;
    padding-right: 40px;
    &__item {
        display: block;
        &--active{
            position: relative;
            .product-categories__link {
                font-weight: 500;
            }
            &::before {
                content: '';
                display: inline-block;
                position: absolute;
                width: 5px;
                height: 24px;
                left: -15px;
                background-color: map_get($blue, default);
            }
        }
    }
    &__link {
        display: block;
        color: map_get($gray, default);
        font-size: 14px;
        font-weight: 400;
        padding: 4px 0;
        transition: .3s;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
    }
}

.product-sub-categories {
    padding-left: 20px;
    padding: 11px 0 11px 20px;
    &__link {
        display: block;
        color: map_get($gray, default);
        font-size: 14px;
        font-weight: 400;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        max-width: 100%;
        padding: 4px 0;
        transition: .3s;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
        &--active {
            color: map_get($gray-light, active);
            &:hover {
                color: map_get($gray-light, active);
            }
            &:active {
                color: map_get($gray-light, active);
            }
        }
    }
}

.catalog-h-line {
    height: 1px;
    background: #ececec;
}


.products-container {
    background-color: #fff;
    .products-container__inner {
        padding: 45px 25px;
        @include clearfix;
        @include products-grid(12px, 30px);
    }
}

.products-container .products-container__inner > div:not(.product-horizontal-line) {
    margin-left: -10px;
    margin-right: -10px;
}
